.preview-wrapper {
	position: relative;
	height: 100%;
	flex-grow: 1;
	overflow: hidden;
}

.react-draggable-transparent-selection .preview-wrapper::after {
	content: "";
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	opacity: 0;
}

.preview-wrapper iframe {
	width: 100%;
	height: 100%;
	background-color: #fff;
	border: none;
}

.preview-error {
	position: absolute;
	top: 20px;
	left: 20px;
	color: red;
}

.preview-error h3 {
	margin-bottom: 10px;
}
